<template>
	<view class="box">
		<view class="good-shop-box">
			<view class="good-shop">
				<view class="top">
					<image lazy-load class="iconfont" :src="merchants_shop.picUrl" mode=""></image>
					<view class="shop-name">
						{{merchants_shop.merchantShopName}}
						<view class="id">
							{{merchants_shop.shopAddress}}
						</view>
					</view>
				</view>
			</view>
			<view class="shop-comein">
				<view class="title">
					店铺收入
				</view>
				<view class="shop-money">
					<view class="money-box">
						<view class="text">
							{{shopTotalMoney | formatNum}}元
						</view>
						<view class="bottom-text">
							店铺交易总额
						</view>
					</view>
					<view class="money-box">
						<view class="text">
							{{shopOrderCount | formatNum}}
						</view>
						<view class="bottom-text">
							下单次数
						</view>
					</view>
				</view>
			</view>
			<view class="goods-box">
				<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
					<view class="goods-li" v-for="(item,k) in data_list" :key="k">
						<navigator :url="'../goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId">
							<image lazy-load class="img" :src="item.mainPicUrl" mode="aspectFill"></image>	
						</navigator>
						<view class="right">
							<navigator :url="'../goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId">
							<view class="title">
								{{item.spuTitle}}{{item.spuTitle}}{{item.spuTitle}}{{item.spuTitle}}{{item.spuTitle}}
							</view>
							<view class="money-box">
								<text class="price font-bold">￥{{item.promptPrice | formatNum}}</text>
								<!-- <text class="num">12498人付款</text> -->
								<text class="status">{{sale_status[item.onSale]}}</text>
							</view>
							<view class="youhui">
								<view class="btn yongjin-btn">
									推广佣金
								</view>
							</view>
							</navigator>
							<view class="set-box">
								<view class="btn bg-primary">
									推广
								</view>
								<view class="btn bg-orange" v-if="item.onSale=='NOSALE'" @tap="putOn(k)">
									上架
								</view>
								<view class="btn bg-red" v-if="item.onSale=='SALE'" @tap="putOn(k)">
									下架
								</view>
							</view>
						</view>
					</view>
				</mescroll-body>
			</view>
		</view>

	</view>
</template>

<script>
	import Vue from 'vue'
	import {mapState} from 'vuex'
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue";
	
	let merchantSeqId = "";
	let merchantShopId = "";
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				shop_list: [],
				upOption: {
					textNoMore: "没有更多数据",
				},
				page_index: 1,
				page_size: 10,
				data_list: [],
				sale_status:{
					'INIT':"",
					'APPLY':"申请中",
					'NOSALE':"未上架",
					'SALE':"已上架",
				},
				shopTotalMoney:0,
				shopOrderCount:0
			}
		},
		components: {
			MescrollBody
		},
		computed:{
			...mapState({
				merchants_shop:state=>state.merchants_shop,
			})
		},
		onLoad(option) {
			merchantSeqId = option.merchantSeqId
			merchantShopId = option.merchantShopId
			this.loadOrderTotal()
		},
		methods: {
			// 上下架
			putOn(k){
				this.$api.get('/o2oSpuOnSale/updateSaleStatusShop',{params:{
					spuSeqId:this.data_list[k].seqId,
					merchantShopId:this.data_list[k].merchantShopId,
					onSale:this.data_list[k].onSale=='SALE'?'NOSALE':'SALE',
				}}).then(res=>{
					if(res.code==200){
						this.$box.toast(res.msg)
						if(this.data_list[k].onSale=='SALE'){
							this.data_list[k].onSale='NOSALE'
						}else {
							this.data_list[k].onSale='SALE'
						}
					}
				}).catch(err=>{
					console.log(err);
				})
			},
			loadOrderTotal(){
				this.$api.get('/order/selectShopSalesVolume',{
					params:{
						merchantShopId:merchantShopId
					}
				}).then(res=>{
					if(res.code==200){
						let data=res.data;
						this.shopTotalMoney=data.shopTotalMoney
						this.shopOrderCount=data.shopOrderCount
					}
				})
			},
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pageno: this.page_index,
						pagesize: this.page_size,
						merchantSeqId: merchantSeqId,
						merchantShopId: merchantShopId
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}
	}
</script>

<style lang="scss">
	.shop-comein {
		width:642rpx;
		background:rgba(255,255,255,1);
		border-radius:10rpx;
		margin: 40rpx auto;
		padding: 30rpx 20rpx;
		font-size: 24rpx;
		.title {
			font-size:30rpx;
		}
		.shop-money {
			display: flex;
			justify-content: space-between;
			.money-box {
				width: 50%;
				height: 140rpx;
				text-align: center;
				position: relative;
				.text {
					width: 100%;
					line-height: 90rpx;
					font-size:36rpx;
					color: #FF6430;
				}
			}
			.money-box:first-child::after{
				position: absolute;
				width:1px;
				height:44rpx;
				content: "";
				background: #C6C6C6;
				top: 50%;
				margin-top: -22rpx;
				right: 0;
				z-index: 1;
			}
			
		}
	}
	.goods-box {
		background: white;

		.goods-li {
			width: calc(100% - 60rpx);
			padding: 40rpx 0;
			margin: 0 auto;
			height: auto;
			display: flex;
			justify-content: space-between;
			overflow: hidden;

			.img {
				width:200rpx;
				height:200rpx;
				flex-shrink: 0;
				margin-right: 20rpx;
				background: #e5e5e5;
				border-radius:8rpx;
			}

			.right {
				width: calc(100% - 220rpx);
				font-size: 30rpx;

				.title {
					width: 100%;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				}

				.money-box {
					width: 100%;
					display: flex;
					justify-content: space-between;
					align-items: center;
					color: #666;
					font-size: 20rpx;
					margin: 30rpx 0;
					.price {
						font-size: 30rpx;
						color: #f53a00;
					}
					.status {
						color:rgba(154,154,154,1);
						font-size:24rpx;
					}
				}

				.yongjin-btn {
					width:130rpx;
					height:48rpx;
					line-height: 48rpx;
					text-align: center;
					border:1px solid rgba(164,106,255,1);
					border-radius:24rpx;
					font-size:26rpx;
					color:rgba(164,106,255,1);
					margin: 10rpx 0;
				}

				.set-box {
					display: flex;
					justify-content: flex-end;
					.btn {
						margin-right: 20rpx;
					}
				}
			}
		}
	}

	.bg-red {
		background: #f6591e;
		color: white;
	}

	.bg-orange {
		background: #feba02;
		color: white;
	}

	.good-shop {
		background: white;
		width: calc(100% - 60rpx);
		padding: 40rpx 30rpx;
		margin-bottom: 30rpx;

		.top {
			display: flex;
			align-items: center;

			.iconfont {
				width: 100rpx;
				height: 100rpx;
				border-radius: 100%;
				background: $color-primary;
				color: white;
				text-align: center;
				line-height: 100rpx;
				font-size: 40rpx;
				margin-right: 26rpx;
			}

			.shop-name {
				font-size: 30rpx;

				.id {
					color: #93979f;
					font-size: 24rpx;
					margin-top: 10rpx;
				}
			}
		}

		.bottom {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-top: 40rpx;

			.money {
				font-size: 20rpx;
			}

			.right-bottom {
				width: 310rpx;
				flex-shrink: 0;
				display: flex;
				justify-content: space-between;
			}

		}
	}

	.btn {
		width: 140rpx;
		height: 60rpx;
		text-align: center;
		line-height: 60rpx;
		border-radius: 40rpx;
		font-size: 28rpx;
	}

	.bg-primary {
		background: $color-primary;
		color: white;
	}

	.border-primary {
		border: 1px solid $color-primary;
		color: $color-primary;
	}
</style>
